/* eslint-disable no-unused-vars */
import { fetchServers } from './myServers/fetch';
import { stopGraphqlClient } from './myServers/client';
import i18n from '../i18n';
import {
  KeyServerWretch, WebguiInstallKey, WebguiUpdate, WebguiState,
} from '../api';
import { log } from '../log';

import {
  OPEN_POPUP, CAN_POST_MESSAGE, SAFE_JSON_PARSE, THE_SMARTASS, WEBGUI_PARENT, ALLOWED_ORIGINS,
} from '../consts';

// eslint-disable-next-line import/prefer-default-export
export const actions = {
  /**
   * @module USERPROFILE
   * @method: openPopUp
   * @description: Open pop-up window or if one already exists we focus on it
   * @param: {string} payload.url
   * @param: {string} payload.name
   * @param: {string} payload.width
   * @param: {string} payload.height
   * @param: {string} payload.nagScreen
   */
  openPopUp({
    commit, dispatch, getters, state,
  }, payload) {
    log.debug('[openPopUp]: %o', payload);
    if (state.popUpWindow) { // re-focus the existing pop-up window
      state.popUpWindow.focus();
    } else { // otherwise create a new one
      const newPopUp = OPEN_POPUP(payload.url, payload.name, payload.width, payload.height);
      const isDarkMode = state.darkMode;
      const isPurchase = payload.url.toString().includes('/preflight');
      const black = '#222';
      const white = '#fff';
      const orangeGradient = 'linear-gradient(to right, #E22828 0%, #ff8c2f 100%)';
      const lightModeBg = (isPurchase ? white : orangeGradient);
      newPopUp.document.body.style.background = isDarkMode ? black : lightModeBg;
      /**
       * @description Pop-up blocker detection
       * @see https://stackoverflow.com/a/27725432/3215809
       */
      try {
        newPopUp.focus();
        // in pop-up blocker detection so vuex doesn't update until we know they will be a pop-up
        // after this the popUp is referenced & used via `state.popUpWindow` instead of `newPopUp`
        commit('CHANGE_POP_UP_WINDOW', newPopUp);
        commit('STATE_AT_OPEN', state.registered);
        /**
         * @description every 250ms
         * - detect if the pop up is closed
         * - sending server session postMessage
         */
        const popupClosed = setInterval(() => {
          if (state.popUpWindow.closed) {
            clearInterval(popupClosed);
            dispatch('resetPopUp', { reloadWebGUI: false });
          }
        }, 250);
        // Detect closure of window that triggered pop-up
        window.onunload = () => {
          if (state.popUpWindow && !state.popUpWindow.closed) state.popUpWindow.close();
        };
      } catch (e) {
        // eslint-disable-next-line no-alert
        alert('Pop-up blocker detected.\nTo continue successfully please add this website to your browser\'s exeception list then refresh.');
      }
    }
  },
  /**
   * @module REGWIZ,USERPROFILE
   * @method: resetPopUp
   * @description: clears cookies related to allowing purchasing. Resets state related to popUpWindow. Refreshes the iframe.
   * @param: {boolean} payload.reloadWebGUI
   */
  resetPopUp({ commit, dispatch }, payload = { reloadWebGUI: true }) {
    log.debug('[resetPopUp]: %o', payload);
    commit('CHANGE_POP_UP_WINDOW', '');
    dispatch('updateUpcFromStatePhp'); // update UPC based on any potential changes that happened during the pop-up's lifespace
    if (payload.reloadWebGUI) dispatch('reloadWebGUI');
  },
  /**
   * @module REGWIZ,USERPROFILE
   * @method: closePopUp
   * @description: Close pop-up window attached to state.popUpWindow
   * @param: {boolean} payload.reloadWebGUI
   * ```
   */
  closePopUp({ dispatch, state }, payload = { reloadWebGUI: false }) {
    log.debug('[closePopUp]: %o', payload);
    // check if we need to close any open pop-up window
    if (state.popUpWindow) {
      state.popUpWindow.close();
      dispatch('resetPopUp', { reloadWebGUI: payload.reloadWebGUI });
    }
  },
  /**
   * @module REGWIZ,USERPROFILE
   * @name reloadWebGUI
   * @description
   */
  reloadWebGUI() {
    log.debug('[reloadWebGUI]: %s', '');
    window.location.reload();
    // dispatch('reloadPostMessage');
  },
  /**
   * @module REGWIZ
   * @method closePopUpFromPopUp
   * @description We have a timer setup in the launchpad to autorefresh when it detects that a popup is closed. So explicitly close the popup just in case.
   */
  closePopUpFromPopUp({ dispatch }) {
    log.debug('[closePopUpFromPopUp]: %o');
    dispatch('resetPopUp');
    window.close();
  },
  /**
   * @module REGWIZ
   * @name sendDataPostMessageToServer
   * @description
   * @param {object} getters - vuex getters / computed state https://vuex.vuejs.org/guide/getters.html
   */
  sendDataPostMessageToServer({ state }) {
    const payload = (state.submissionFrom === 'SignOut') ? { event: 'MYSERVERS_UNREGISTER' }
      : {
        event: 'REG_WIZARD',
        keyfile: state.keyfile,
        license: state.license,
        email: state.userEmail,
        apikey: state.apikey,
        avatar: state.avatar,
        username: state.username,
        guid: state.guid, // using for `regWizTime` in plg
      };
    log.debug('[sendDataPostMessageToServer]: %o', payload);
    try {
      WEBGUI_PARENT.postMessage(JSON.stringify(payload), '*');
    } catch (error) {
      if (!CAN_POST_MESSAGE) log.warn('[sendDataPostMessageToServer] - no WEBGUI_WINDOW', payload);
      log.error(error);
    }
  },
  /**
   * @module USERPROFILE
   * @method: receivePostMessage
   * @description: handles postMessages to the app
   * @param: {Object} payload
   * @param: {boolean} payload.inject - if true we inject into the state behind the scenes
   * @param: {EventObject} payload.event - postMessage event. See mdn: https://developer.mozilla.org/en-US/docs/Web/API/Window/postMessage#The_dispatched_event
   * @param: {String} payload.event.data - MUST BE STRING AS WE ARE EXPECTING TO PARSE STRING TO JSON
   */
  receivePostMessage({ // eslint-disable-next-line no-unused-vars
    commit, dispatch, getters, state,
  }, payload = { inject: false, event: null }) {
    // log.debug('[receivePostMessage]: %o', payload);
    if (!payload.event) return;
    const [err, data] = SAFE_JSON_PARSE(payload.event.data);
    if (err) return;
    /**
     * Handles key install for webgui
     */
    const installKey = (keyData) => {
      if (keyData) {
        WebguiInstallKey
          .query({ url: keyData })
          .get()
          .res(() => {
            log.log(`New license key installed: ${keyData}`);
            const pingbackPayload = {
              event: 'LICENSE_PINGBACK',
              message: 'New license key installed',
              license: keyData,
              success: true,
            };
            payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
          })
          .catch((error) => {
            log.error(`Failed to license new key: ${keyData}`, error);
            const pingbackPayload = {
              event: 'LICENSE_PINGBACK',
              message: 'Failed to license new key',
              license: keyData,
              success: false,
            };
            payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
          });
      } else {
        log.error('[KEY_PURCHASE event fired] missing license data: %s', keyData);
      }
    };
    /**
     * @note
     * if we check origin here like `if (!ALLOWED_ORIGINS.includes(payload.event.origin)) return`
     * then this would potentially block our pop-up window from receiving a ping back response from the webGUI.
     * This is why we aren't checking postmessage event origin here
     */
    switch (data.event) {
    case 'SESSION':
      log.debug('[receivePostMessage.SESSION]: %o', data);
      commit('SET_FULL_SERVER_STATE', data.server); // set data into Vuex
      i18n.mergeLocaleMessage(data.locale, data.messages); // add messages into i18n
      i18n.locale = data.locale; // set locale to received locale
      log.debug(`[receivePostMessage.SESSION] ${i18n.locale} locale & messages set`, i18n.messages);
      break;
    case 'PREFLIGHT_REQUEST':
      log.debug('[receivePostMessage.PREFLIGHT_REQUEST]: %o', data);
      if (!ALLOWED_ORIGINS.includes(payload.event.origin)) { // ensure the requester is allowed, otherwise we say, NO NO!
        log.error('[ ACCESS DENIED]');
        break;
      }
      /**
         * @note - if you update anything here please double check both places that receives
         * this postMessage back to the sender of 'PREFLIGHT_REQUEST':
         * - swtich `case 'SESSION':` where we set `data.server` & `data.messages`
         * - @unraid/craft-unraid * `web/helpers/preflight.js`
         */
      payload.event.source.postMessage(JSON.stringify({
        event: 'SESSION',
        server: {
          ...getters.server,
        },
        // this feeds the existing locale data from UPC to the pop-up
        locale: i18n.locale,
        messages: {
          ...i18n.messages[i18n.locale],
        },
      }), payload.event.origin);
      break;
    case 'LICENSE_PINGBACK':
      log.debug('[receivePostMessage.LICENSE_PINGBACK]: %o', data);
      commit('LICENSE_PINGBACK', data);
      break;
    case 'ACCOUNT_PINGBACK':
      log.debug('[receivePostMessage.ACCOUNT_PINGBACK]: %o', data);
      commit('ACCOUNT_PINGBACK', data);
      break;
    case 'DELIVER_NEW_STATE':
      log.debug('[receivePostMessage.DELIVER_NEW_STATE]: %o', data);
      commit('SET_FULL_SERVER_STATE', data);
      commit('RECEIVED_SUCCESS_NEW_SERVERSTATE', Date.now());
      break;
    // these postMessage events we're originally handled in the webgui PLG
    case 'KEY_PURCHASE':
      log.debug('[receivePostMessage.KEY_PURCHASE]: %o', data);
      installKey(data.license);
      break;
    case 'MYSERVERS_UNREGISTER':
      log.debug('[receivePostMessage.MYSERVERS_UNREGISTER]: %o', data);
      WebguiUpdate
        .formUrl({
          csrf_token: state.csrf,
          '#file': 'Unraid.net/myservers.cfg',
          '#section': 'remote',
          apikey: '',
          avatar: '',
          email: '',
          username: '',
        })
        .post()
        .res(() => {
          log.log('Unraid.net/myservers.cfg: Unregistered myservers, cleared apikey under [remote] section');
          // send a ping back to the regwiz
          const pingbackPayload = {
            event: 'ACCOUNT_PINGBACK',
            success: true,
            type: 'signOut',
            webGuiPathname: window.location.pathname,
          };
          payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
        })
        .catch((error) => {
          log.error('Failed to unregister');
          // send a ping back to the regwiz
          const pingbackPayload = {
            event: 'ACCOUNT_PINGBACK',
            success: false,
            type: 'signOut',
            webGuiPathname: window.location.pathname,
          };
          payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
        });
      break;
    case 'REG_WIZARD':
      log.debug('[receivePostMessage.REG_WIZARD]: %o', data);
      if (data.apikey) {
        const postargs = {
          csrf_token: state.csrf,
          '#file': 'Unraid.net/myservers.cfg',
          '#section': 'remote',
          apikey: data.apikey,
          regWizTime: `${Date.now()}_${data.guid}`, // set when signing in the first time and never unset for the sake of displaying Sign In/Up in the UPC without needing to validate guid every time
        };
        if (data.email) {
          postargs.email = data.email;
        }
        if (data.username) {
          postargs.username = data.username;
        }
        if (data.avatar) {
          postargs.avatar = data.avatar;
        }
        // duplicate conditional so we don't get the error from installKey()
        if (data.license) installKey(data.license);
        WebguiUpdate
          .formUrl(postargs)
          .post()
          .res(() => {
            log.log('Unraid.net/myservers.cfg: Updated apikey under [remote] section');
            // send a ping back to the regwiz
            const pingbackPayload = {
              event: 'ACCOUNT_PINGBACK', success: true, type: 'signIn', webGuiPathname: window.location.pathname,
            };
            payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
          })
          .catch(() => {
            log.error('Failed to update apikey under [remote] section');
            // send a ping back to the regwiz
            const pingbackPayload = {
              event: 'ACCOUNT_PINGBACK', success: false, type: 'signIn', webGuiPathname: window.location.pathname,
            };
            payload.event.source.postMessage(JSON.stringify(pingbackPayload), payload.event.origin);
          });
      }
      break;
    case 'RELOAD':
      log.debug('[receivePostMessage.RELOAD]: %o', data);
      window.location.reload();
      break;
    case 'GET_STATE':
      log.debug('[receivePostMessage.GET_STATE]: %o', data);
      WebguiState
        .url(state.stateEndpoint)
        .get()
        .json()
        .then((json) => {
          payload.event.source.postMessage(json, payload.event.origin);
        })
        .catch((error) => {
          log.error(`Failed to get new state: ${error}`);
        });
      break;
    case 'SUCCESS_NEW_STATE':
      log.debug('[receivePostMessage.SUCCESS_NEW_STATE]: %o', data);
      WebguiState
        .url(state.stateEndpoint)
        .get()
        .json()
        .then((json) => {
          const newPayload = { ...json, event: 'DELIVER_NEW_STATE', success: true };
          payload.event.source.postMessage(JSON.stringify(newPayload), payload.event.origin);
        })
        .catch((error) => {
          log.error('[SUCCESS_NEW_STATE] %o', error);
          const failPayload = { event: 'DELIVER_NEW_STATE', success: false };
          payload.event.source.postMessage(JSON.stringify(failPayload), payload.event.origin);
        });
      break;
    case 'REDIRECT_MAIN':
      log.debug('[receivePostMessage.REDIRECT_MAIN]: %o', data);
      window.location.href = '/Main';
      break;
    default: log.debug('[receivePostMessage.UNHANDLED_MESSAGE]: %o', data);
    }
  },
  /**
   * @module USERPROFILE
   * @method: getStatePostMessage
   * @description: Pings the webGUI with a postMessage for the server state `receivePostMessage` handles the response back.
   * @param {string} event - postMessage event type expected by receiver
   */
  getStatePostMessage({ state }, event) {
    const payload = { event: event || 'GET_STATE' };
    log.debug('[getStatePostMessage]: %o', payload);
    if (CAN_POST_MESSAGE) WEBGUI_PARENT.postMessage(JSON.stringify(payload), state.site);
    else log.warn('[getStatePostMessage] - `suppress post messages`', CAN_POST_MESSAGE);
  },
  /**
   * @module
   * @name reloadPostMessage
   * @description
   * @param {object} getters - vuex getters / computed state https://vuex.vuejs.org/guide/getters.html
   */
  reloadPostMessage({ state }) {
    const payload = { event: 'RELOAD' };
    log.debug('[getStatePostMessage]: %o', payload);
    if (CAN_POST_MESSAGE) WEBGUI_PARENT.postMessage(JSON.stringify(payload), state.site);
    else log.warn('[reloadPostMessage] - no WEBGUI_WINDOW to reload');
  },
  /**
   * @module USERPROFILE
   * @param {stringify json object} payload  this.serverstate
   */
  setStateFromWebComponentProp({ commit }, payload) {
    // decode the encoded prop, then try to parse as JSON
    const [err, data] = SAFE_JSON_PARSE(decodeURIComponent(payload));
    if (err) return log.error('[getStatePostMessage]: %o', payload);
    log.debug('[setStateFromWebComponentProp]: %o', data);
    return commit('SET_FULL_SERVER_STATE', data);
  },
  /**
   * @module USERPROFILE
   */
  async updateUpcFromStatePhp({ commit, state }) {
    log.debug('[updateUpcFromStatePhp]: %s', '01-start');
    const newState = await WebguiState
      .url(state.stateEndpoint)
      .get()
      .json()
      .catch((error) => {
        /** @idea - potentially do `error.toString()` to prevent issues down the line
         * Unexpected token === server is running but CSRF token is wrong or something else is causing a redirect to home
         * 503 rate limited at this. stop polling.
        */
        if (error.toString().includes('Unexpected token') || error.toString().includes('503')) {
          // Session is invalid, user has logged out from another tab
          window.location.href = '/'; // stop all polling, do NOT pass GO, DO NOT COLLECT $200
        } else if (error === 'TypeError: Failed to fetch') { // do nothing, server is probably rebooting
        } else { // some other error
          log.error('[pingStateEndpoint.catch] %o', error);
        }
      })
      .then(res => commit('SET_FULL_SERVER_STATE', res));
    return newState;
  },
  /**
   * @module LAUNCHPAD,REGWIZ
   * @param {string} guid
   */
  async validateGuid({ commit, getters, state }) {
    await new Promise((resolve, reject) => {
      log.debug('[validateGuid]');
      /**
       * If the server has been registered before see if the GUID in the value saved matches
       * if so DO NOT ping the key server
       * - but we should always ping when stateData.error
       */
      const previouslyRegistered = (state.regWizTime) ? (state.regWizTime.split('_')[1] === state.guid) : false;
      if (previouslyRegistered && !getters.stateData.error) {
        commit('CHANGE_GUID_REGISTERED', true); /** @important must come before `CHANGE_GUID_VALID` */
        commit('CHANGE_GUID_VALID', true);
        commit('SET_LOADING_STATE', false);
        return resolve(true);
      }
      /**
       * @NOTE  DO NOT do `commit('SET_LOADING_STATE', true);`
       * Unless you want to create an infinite loop in the pop-up Index.vue page
       */
      let errorMessage = 'Invalid GUID';
      if (!state.guid) {
        errorMessage = `${errorMessage} not present`;
        commit('CHANGE_GUID_BLACKLISTED', true);
        commit('OVERRIDE_SERVER_STATE', THE_SMARTASS);
        commit('SET_LOADING_STATE', false);
        return reject(new Error(errorMessage));
      }
      const tweleveZeros = state.guid.includes('000000000000'); // 12 consecutive zeros
      // quick client side validation to prevent unnecessary endpoint POST
      if (tweleveZeros || state.guid.length !== 27) {
        commit('CHANGE_GUID_BLACKLISTED', true);
        if (tweleveZeros) {
          errorMessage += ': Too many consecutive zeros';
          commit('OVERRIDE_SERVER_STATE', 'EBLACKLISTED2');
        } else {
          errorMessage += ': Not long enough';
          commit('OVERRIDE_SERVER_STATE', 'EBLACKLISTED1');
        }
        commit('SET_LOADING_STATE', false);
        return reject(new Error(errorMessage));
      }
      /**
       * ping validation endpoint
       * - when 'EGUID' we need to validate the registered GUID not the current flash GUID.
       * - Otherwise the `replaceable` value that's return will always be `false` since the current flash GUID will theoretically be new.
       */
      const guidForValidation = state.state === 'EGUID' ? state.regGuid : state.guid;
      const validate = KeyServerWretch.url('/validate/guid')
        .formUrl({ guid: guidForValidation })
        .post()
        .forbidden((err) => {
          // if the response error message includes 'blacklist' we know the server is blacklisted
          if (err.message.includes('blacklisted')) {
            commit('CHANGE_GUID_BLACKLISTED', true);
            commit('OVERRIDE_SERVER_STATE', 'EBLACKLISTED');
          }
          // to ensure we show the correct error state for the user if they dont have a valid GUID or there was no GUID passed
          if (err.message.includes('invalid GUID')) {
            commit('CHANGE_GUID_BLACKLISTED', true);
            commit('OVERRIDE_SERVER_STATE', 'EBLACKLISTED1');
          }
        })
        .json((json) => {
          if (json.registered) commit('CHANGE_GUID_REGISTERED', json.registered); /** @important must come before `CHANGE_GUID_VALID` */
          if (json.forumaccount) commit('CHANGE_GUID_FORUMACCOUNT', json.forumaccount);
          commit('CHANGE_GUID_VALID', true); // successful response means the GUID is valid.
          commit('CHANGE_REPLACEABLE', json.replaceable);
        })
        .catch(err => reject(new Error(err)))
        .finally(() => {
          commit('SET_LOADING_STATE', false);
        });

      return resolve(validate);
    });
  },
  jumpToUpc({ commit }) {
    // scroll to top
    window.scrollTo({ top: 0, behavior: 'smooth' });
    // open the UPC
    commit('TRIGGER_UPC', true);
    // reset `state.triggerUPC` after Xms
    setTimeout(() => {
      commit('TRIGGER_UPC', false);
    }, 500);
  },
  fetchServers,
  stopGraphqlClient,
};
