<template>
  <div class="text-center w-full">
    <header>
      <h2 v-html="stateData.heading ? stateData.heading : stateData.error.heading" class="text-24px md:text-30px"></h2>
      <div
        v-html="stateData.message ? stateData.message : stateData.error.message"
        class="text-14px opacity-90 mt-12px mx-auto"
        :class="{
          'text-center': stateData.messageCenter,
          'text-left': !stateData.messageCenter,
          'prose-2xl': !CAN_POST_MESSAGE,
          'prose': CAN_POST_MESSAGE,
        }"
      >
        <slot></slot>
      </div>
    </header>
    <div class="ActionRequired flex flex-col justify-center mt-16px px-16px">
      <template v-if="isPopUpOpen">
        <ConditionalButton
          @btn-click="openPopUp(signInPayload)"
          :text="$t('backToPopUp')"
          class="button button-alt w-full mt-8px" />
        <ConditionalButton
          @btn-click="closePopUp()"
          :text="$t('closePopUp')"
          class="button-fine-error text-grey-mid w-full mt-8px" />
      </template>
      <template v-else>
        <template v-if="stateData.actions.includes('register')">
          <ConditionalButton
            @btn-click="openPopUp(signInPayload)"
            :routeName="CAN_POST_MESSAGE ? 'SignIn' : null"
            :text="$t('signIn')"
            class="button button-alt w-full mt-8px" />
          <ConditionalButton
            v-if="!guidRegistered"
            @btn-click="openPopUp(signUpPayload)"
            :routeName="CAN_POST_MESSAGE ? 'SignUp' : null"
            :text="$t('signUp')"
            class="button button-alt w-full mt-8px" />
        </template>
        <!-- purchase / upgrade subset of buttons -->
        <ConditionalButton
          v-if="stateData.actions.includes('purchase')"
          @btn-click="openPopUp(purchaseWindow)"
          :hrefUrl="CAN_POST_MESSAGE ? purchaseWindow.url : null"
          :text="$t('actions.purchase')"
          class="button button-alt w-full mt-8px" />
        <ConditionalButton
          v-if="stateData.actions.includes('upgrade')"
          @btn-click="openPopUp(purchaseWindow)"
          :hrefUrl="CAN_POST_MESSAGE ? purchaseWindow.url : null"
          :text="$t('actions.upgrade')"
          class="button button-alt w-full mt-8px" />
        <!-- replace / recover key -->
        <ConditionalButton
          v-if="stateData.actions.includes('replace') && replaceable"
          @btn-click="openPopUp(replaceKeyPayload)"
          :routeName="CAN_POST_MESSAGE ? 'ReplaceKey' : null"
          :text="$t('actions.replace')"
          class="button w-full mt-8px" />
        <ConditionalButton
          v-if="stateData.actions.includes('replace') && !replaceable"
          :text="$t('actions.replaceIneligible')"
          :title="$t('stateData.EGUID.error.message.replacementIneligible')"
          disabled
          @btn-click="niceTry()"
          class="button w-full mt-8px" />
        <ConditionalButton
          v-if="stateData.actions.includes('recover')"
          @btn-click="openPopUp(recoverKeyPayload)"
          :routeName="CAN_POST_MESSAGE ? 'RecoverKey' : null"
          :text="$t('actions.recover')"
          class="button w-full mt-8px" />
        <!-- trial related buttons -->
        <ConditionalButton
          v-if="stateData.actions.includes('continueTrial')"
          @btn-click="CAN_POST_MESSAGE ? closePopUpFromPopUp() : SET_IS_LAUNCHPAD_OPEN(false)"
          :text="$t('regWizPopUp.continueTrial')"
          class="button w-full mt-8px" />
        <ConditionalButton
          v-if="stateData.actions.includes('extend')"
          @btn-click="openPopUp(extendTrialPayload)"
          :routeName="CAN_POST_MESSAGE ? 'ExtendTrial' : null"
          :text="$t('actions.extend')"
          class="button w-full mt-8px" />
        <!-- glorified close button -->
        <ConditionalButton
          v-if="stateData.actions.includes('close')"
          @btn-click="CAN_POST_MESSAGE ? closePopUpFromPopUp() : SET_IS_LAUNCHPAD_OPEN(false)"
          :text="$t('continueToUnraid')"
          class="button w-full mt-8px" />
        <!-- @note - `&& CAN_POST_MESSAGE` ensures that we only display Sign Out in a pop-up -->
        <ConditionalButton
          v-if="stateData.actions.includes('signOut') && CAN_POST_MESSAGE"
          @btn-click="openPopUp(signOutPayload)"
          :routeName="CAN_POST_MESSAGE ? 'SignOut' : null"
          :text="$t('signOut')"
          class="button-fine-error text-grey-mid w-full mt-8px" />
      </template>
    </div>
  </div>
</template>

<script>
import {
  mapActions,
  mapGetters,
  mapMutations,
  mapState,
} from 'vuex';

import { CAN_POST_MESSAGE } from '../../consts';
import ConditionalButton from './ConditionalButton.vue';

export default {
  components: {
    ConditionalButton,
  },
  data: () => ({
    CAN_POST_MESSAGE,
  }),
  computed: {
    ...mapGetters(['stateData']),
    ...mapState([
      'isPopUpOpen',
      'purchaseWindow',
      'guidRegistered',
      'extendTrialPayload',
      'recoverKeyPayload',
      'registered',
      'replaceable',
      'replaceKeyPayload',
      'signOutPayload',
      'signInPayload',
      'signUpPayload',
      'state',
      'wizardWindow',
      'messages',
    ]),
  },
  methods: {
    ...mapActions([
      'openPopUp',
      'closePopUp',
      'closePopUpFromPopUp',
    ]),
    ...mapMutations([
      'SET_IS_LAUNCHPAD_OPEN',
    ]),
    niceTry() {
      // eslint-disable-next-line no-alert
      alert(' Nice try you little sneak ');
    },
  },
};
</script>

<style lang="postcss">
.ActionRequired a,
.ActionRequired button {
  @apply text-14px px-24px py-16px;
}
</style>
