import { ApolloClient } from 'apollo-client';
import { ApolloLink, split } from 'apollo-link';
import { setContext } from 'apollo-link-context';
import { HttpLink } from 'apollo-link-http';
import { InMemoryCache } from 'apollo-cache-inmemory';
import { WebSocketLink } from 'apollo-link-ws';
import { getMainDefinition } from 'apollo-utilities';
import { onError } from 'apollo-link-error';
import { RetryLink } from 'apollo-link-retry';
import * as Sentry from '@sentry/vue';

import { DEV_GRAPH_URL, NODE_ENV } from '../../env';
import { log } from '../../log';

const WINDOW_URL = new URL(DEV_GRAPH_URL ?? window.location.origin);

const clients = {};

const createGraphqlClient = (context) => {
  log.debug('[createGraphqlClient]');
  /**
   * Getting response headers
   * @see: https://stackoverflow.com/questions/54990173/graphql-cannot-read-response-headers-in-apollo-client
   */
  const authware = new ApolloLink((operation, forward) => forward(operation).map((response) => {
    const {
      response: { headers },
    } = operation.getContext();
    if (headers) {
      const xEnv = headers.get('x-environment') ?? null;
      context.commit('SET_MY_SERVERS_ENV', xEnv);
    }
    return response;
  }));

  const httpLink = authware.concat(new HttpLink({ // HTTP connection to the API
    uri: `${WINDOW_URL.protocol}//${WINDOW_URL.host}/graphql`, // note the additional '//' slashes here for the URL
  }));
  // Create the subscription websocket link
  const wsProtocol = WINDOW_URL.protocol.includes('https') ? 'wss://' : 'ws://';
  const wsLinkUri = `${wsProtocol}${WINDOW_URL.host}/graphql`;
  const wsLink = new WebSocketLink({
    uri: wsLinkUri,
    options: {
      timeout: 65000,
      reconnect: true,
      connectionParams: {
        'x-api-key': context.state.apikey,
      },
    },
  });
  /**
   * Monitor websocket connection
   */
  let disconnectTimeout;
  wsLink.subscriptionClient.maxConnectTimeGenerator.setMin(3000);
  wsLink.subscriptionClient.onConnected(() => {
    log.debug('[ wslink.onConnected ]');
    context.commit('SET_GRAPH_ONLINE', true);
    if (context.state.myServersError) context.commit('SET_MY_SERVERS_ERROR', null);
  });
  wsLink.subscriptionClient.onReconnected(() => {
    log.debug('[ wslink.onReconnected ]');
    context.commit('SET_GRAPH_ONLINE', true);
    if (context.state.myServersError) context.commit('SET_MY_SERVERS_ERROR', null);
  });
  wsLink.subscriptionClient.onDisconnected(() => {
    if (context.state.graphOnline) { /** prevents below from firing on first load && when navigating away from the page */
      clearTimeout(disconnectTimeout);
      disconnectTimeout = setTimeout(() => { /** prevents below from firing when navigating away from the page */
        log.debug('[ wslink.onDisconnected ]');
        context.commit('SET_GRAPH_ONLINE', false);
        /** So we don't to overwrite "Graphql is offline" error message */
        if (!context.state.myServersError) context.commit('SET_MY_SERVERS_ERROR', 'Disconnected from Unraid API');
      }, 150);
    }
  });

  const link = split( // using the ability to split links, you can send data to each link depending on what kind of operation is being sent
    ({ query }) => {
      const definition = getMainDefinition(query);
      return definition.kind === 'OperationDefinition'
        && definition.operation === 'subscription';
    },
    wsLink,
    httpLink,
  );
  const authLink = setContext((_, { headers }) => ({
    headers: {
      ...headers,
      'x-api-key': context.state.apikey,
    },
  }));
  /**
   * @idea / @todo
   * - detection specific errors to slow down any polling when hitting connection errors
   * - slow down retry attempt rate
   */
  const errorLink = onError(({ graphQLErrors, networkError }) => {
    if (graphQLErrors) {
      log.error('[GraphQL error] %o', graphQLErrors);
      const msg = (graphQLErrors[0].message || graphQLErrors[0].error.message) ?? 'No error object - [UNRAID]';
      context.commit('SET_MY_SERVERS_ERROR', msg);
      /** send specific issue to Sentry with a special tag */
      if (context.state.sentryEnabled
          && msg.includes('guest doesn\'t have permission to access "servers"')) {
        log.debug('[SPECIAL  SENTRY] ');
        Sentry.withScope((scope) => {
          scope.setTag('specialTracking', 'guestNoPermissionsToAccessServers');
          scope.setLevel('error');
          Sentry.captureException(new Error(msg));
        });
      }
    }
    if (networkError) {
      log.error('[Network error]', networkError);
      context.commit('SET_MY_SERVERS_ERROR', networkError.message);
    }
    context.commit('SET_MY_SERVERS_LOADING', false);
  });

  const retryLink = new RetryLink({
    delay: {
      initial: 300,
      max: Infinity,
      jitter: true,
    },
    attempts: {
      max: Infinity,
      retryIf: (error, _operation) => {
        log.debug('[retryLink - retryIf]', { error, _operation });
        context.commit('SET_GRAPH_ONLINE', false);
        return !!error;
      },
    },
  });

  return new ApolloClient({
    link: ApolloLink.from([retryLink, errorLink, authLink, link]),
    cache: new InMemoryCache(),
    connectToDevTools: NODE_ENV !== 'production',
  });
};

// eslint-disable-next-line import/prefer-default-export
export const getGraphqlClient = (context) => {
  if (!context.state.apikey) return log.debug('[getGraphqlClient] NO API KEY');
  if (Object.keys(clients).includes(context.state.apikey)) { // Return existing client
    log.debug('[getGraphqlClient] found existing client');
    return clients[context.state.apikey];
  }
  log.debug('[getGraphqlClient] new client inbound');
  const client = createGraphqlClient(context);
  clients[context.state.apikey] = client;
  return client;
};

export const stopGraphqlClient = (apikey) => {
  if (!Object.keys(clients).includes(apikey)) return log.debug('[getGraphqlClient] client does not exist for apikey %s', apikey);
  log.debug('[getGraphqlClient] found existing client & stopping it');
  return clients[apikey];
};
