<script>
import { mapActions, mapGetters, mapState } from 'vuex';
import { UnraidLoading } from '@unraid/shared-components';
import UnraidSvgUiWarningFill from '@unraid/shared-components/assets/svg/ui/warning-fill.svg';
import UnraidSvgUtilCheck from '@unraid/shared-components/assets/svg/util/check.svg';
import UnraidSvgUtilDelete from '@unraid/shared-components/assets/svg/util/delete.svg';
import UnraidSvgUtilHelp from '@unraid/shared-components/assets/svg/util/help.svg';
import UnraidSvgUtilInfo from '@unraid/shared-components/assets/svg/util/info.svg';
import UnraidSvgUtilNewWindow from '@unraid/shared-components/assets/svg/util/new-window.svg';

import { log } from '../../log';

export default {
  components: {
    UnraidLoading,
    UnraidSvgUiWarningFill,
    UnraidSvgUtilCheck,
    UnraidSvgUtilDelete,
    UnraidSvgUtilHelp,
    UnraidSvgUtilInfo,
    UnraidSvgUtilNewWindow,
  },
  props: ['errorStateMessageVisible', 'hideotherservers', 'hideMyServers'],
  watch: {
    apikey(newVal, oldVal) {
      if (!oldVal && newVal) { // sign in
        log.debug('[apikey set] connect to unraid-api');
        this.fetchServers();
      }
      if (oldVal && !newVal) { // sign out
        log.debug('[apikey removed] disconnect from unraid-api');
        this.stopGraphqlClient(oldVal);
      }
    },
    /**
     * There was previously an error, but now there's not. If there isn't my servers data,
     * then we need to attempt to fetch servers
     */
    myServersError(newVal, oldVal) {
      if (oldVal && !newVal && !this.myServers.length) this.serversPlz();
    },
  },
  computed: {
    ...mapGetters(['stateData']),
    ...mapState([
      'apikey',
      'guid',
      'graphOnline',
      'myServers',
      'myServersLoading',
      'myServersError',
      'servername',
      'state',
      'wanip',
    ]),
    validApiKeyLength() {
      return this.apikey.length === 64;
    },
    currentServer() {
      if (!this.myServers) {
        return {
          name: this.servername,
          status: 'offline',
        };
      }
      return this.myServers.filter(x => ((x.guid === this.guid) ? x : null))[0];
    },
    currentServerStatus() {
      return this.currentServer ? this.currentServer.status : 'offline';
    },
    filteredServers() {
      if (!this.myServers) return [];
      return (this.hideotherservers) ? [] : this.myServers
        .filter(x => ((x.guid !== this.guid) ? x : null)) // remove current server
        .sort((a, b) => a.name.localeCompare(b.name, 'en', { numeric: true, sensitivity: 'base' })); // sort them homies alphabetically
    },
    errorToggleText() {
      if (this.stateData.error.message) return this.state === 'EEXPIRED' ? this.stateData.error.heading : this.$t('fixError');
      if (this.myServersError) return this.computedErrorHeading;
      return '';
    },
    errorToggleTitle() {
      if (this.stateData.error || this.stateData.message || this.myServersError) {
        const start = (this.stateData.error.message || this.stateData.message)
          ? this.stateData.humanReadable : this.computedErrorHeading;
        const end = this.errorStateMessageVisible ? this.$t('upc.myServers.closeDetails') : this.$t('learnMore');
        return `${start}  ${end}`;
      }
      return '';
    },
    computedErrorHeading() {
      if (this.stateData.error.heading) return this.stateData.error.heading;
      if (this.myServersError) {
        return this.myServersError.includes('Network error')
          ? this.$t('upc.myServers.errors.unraidApi.heading')
          : this.$t('upc.myServers.errors.myServers.heading');
      }
      return '';
    },
    computedError() {
      if (this.stateData.error.message) return this.stateData.error.message;
      if (this.myServersError) {
        return this.myServersError.includes('Network error')
          ? this.$t('upc.myServers.errors.unraidApi.message')
          : this.myServersError;
      }
      return '';
    },
  },
  methods: {
    ...mapActions(['fetchServers', 'stopGraphqlClient']),
    serversPlz() {
      if (this.validApiKeyLength) this.fetchServers();
      else if (this.apikey) log.error('[MyServers] invalid apikey');
    },
  },
  created() {
    if (!this.hideMyServers) this.serversPlz();
  },
};
</script>

<template>
  <div class="relative">
    <ul class="relative">
      <!-- Current Server when ERROR STATE -->
      <li v-if="filteredServers.length && myServersError" class="text-red text-12px font-semibold px-16px pb-4px">
        <em>{{ $t('upc.myServers.displayingLastKnown') }}</em>
      </li>
      <li v-if="computedError" key="current-server-error" class="w-full">
        <button
          @click="$emit('toggle-error')"
          class="btn-error group py-8px px-16px flex flex-col"
          :title="errorToggleTitle">
          <div class="w-full flex flex-row justify-between items-center">
            <span class="inline-flex flex-row items-center">
              <UnraidSvgUiWarningFill class="fill-current w-12px h-12px" />
              <span class="font-semibold mx-8px">
                {{ servername }}
              </span>
            </span>
            <UnraidSvgUtilDelete v-if="computedError && errorStateMessageVisible" class="text-current opacity-100 group-hover:opacity-100 transition-opacity duration-200 ease-in-out fill-current w-12px h-12px ml-8px" />
            <UnraidSvgUtilInfo v-else class="text-current opacity-100 group-hover:opacity-100 transition-opacity duration-200 ease-in-out fill-current w-12px h-12px ml-8px" />
          </div>
          <!-- ERROR MESSAGE -->
          <transition name="fade" mode="out-in">
            <div v-if="computedError && errorStateMessageVisible" class="UnraidUPC-serverError">
              <div class="text-14px font-semibold pt-4px">
                <h3 class="text-16px mb-4px">{{ errorToggleText }}</h3>
                <p v-html="computedError"></p>
              </div>
            </div>
          </transition>
        </button>
      </li>
      <!-- display contact support btn only when stateData.error not API related error -->
      <transition name="fade" mode="out-in">
        <li v-if="state !== 'EEXPIRED' && stateData.error && errorStateMessageVisible">
          <a @click="$emit('contact-support')" :title="$t('upc.opensNewHttpsWindow.base', [$t('contactSupport')])" class="link-error group flex flex-row items-center justify-between">
            <span class="inline-flex flex-row items-center">
              <UnraidSvgUtilHelp class="fill-current w-12px h-12px mr-8px" />
              {{ $t('contactSupport') }}
            </span>
            <UnraidSvgUtilNewWindow class="opacity-0 group-hover:opacity-100 transition-opacity duration-200 ease-in-out fill-current w-12px h-12px ml-8px" />
          </a>
        </li>
      </transition>
      <!-- CURRENT SERVER - a-okay -->
      <li
        v-if="!computedError"
        key="current-server"
        class="w-full py-8px px-16px flex flex-row items-center"
        :class="{
          'text-beta': currentServerStatus === 'online',
          'text-orange cursor-not-allowed': currentServerStatus !== 'online',
        }"
        :title="`${$t('upc.myServers.accessLabels.current')}  ${currentServerStatus !== 'online' ? $t('upc.myServers.mothership.notConnected') : $t('upc.myServers.mothership.connected')}`">
        <!-- <span v-if="currentServerStatus !== 'online'" class="text-red text-14px w-12px">&times;</span> -->
        <UnraidSvgUiWarningFill v-if="currentServerStatus !== 'online'" class="fill-current w-12px h-12px" />
        <UnraidSvgUtilCheck v-else class="fill-current w-12px h-12px" />
        <span class="ml-8px select-all">{{ currentServer ? currentServer.name : servername }}</span>
      </li>
      <transition-group name="fade" mode="out-in">
        <UnraidLoading
          v-if="myServersLoading"
          class="w-36px my-8px mx-auto"
          :height="21"
          :title="$t('upc.myServers.loading')"
          key="gql-loading" />
        <li v-else v-for="(server, index) in filteredServers" :key="`filteredServer_${index}`">
          <!-- online, mismatched WAN, remoteurl -->
          <a
            v-if="server.status === 'online' && wanip !== server.wanip && server.remoteurl"
            :href="server.remoteurl"
            class="w-full flex flex-row items-center"
            tabindex="0"
            :title="$t('upc.myServers.accessLabels.remote')">
            <span class="block w-12px h-12px mr-8px bg-green rounded-full"></span>
            <span class="select-all">{{ server.name }}</span>
          </a>
          <!-- online, matching WAN, localurl -->
          <a
            v-else-if="server.status === 'online' && wanip === server.wanip && server.localurl"
            :href="server.localurl"
            class="w-full flex flex-row items-center"
            tabindex="0"
            :title="$t('upc.myServers.accessLabels.local')">
            <span class="block w-12px h-12px mr-8px bg-green rounded-full"></span>
            <span class="select-all">{{ server.name }}</span>
          </a>
          <!-- not connected to momma or no URL -->
          <span
            v-else-if="server.status !== 'never_connected'"
            class="w-full flex flex-row items-center px-16px py-8px opacity-50 cursor-not-allowed"
            :title="server.status === 'online' ? $t('upc.myServers.accessLabels.unavailable') : $t('upc.myServers.accessLabels.offline')">
            <span class="text-red text-14px w-12px mr-8px">&times;</span>
            <span class="select-all">{{ server.name }}</span>
          </span>
        </li>
      </transition-group>
    </ul>
  </div>
</template>
