import Vue from 'vue';
import * as Sentry from '@sentry/vue';
import transform from 'lodash/transform';

import { NODE_ENV } from './env';
import { log } from './log';

const mapExploreDeep = (obj, iterateeReplace, iterateeExplore = () => true) => transform(obj, (acc, value, key) => {
  const replaced = iterateeReplace(value, key, obj);
  const explore = iterateeExplore(value, key, obj);
  if (explore !== false && replaced !== null && typeof replaced === 'object') {
    acc[key] = mapExploreDeep(replaced, iterateeReplace, iterateeExplore);
  } else {
    acc[key] = replaced;
  }
  return acc;
});

const findReplace = (a, b, c) => ((String(a).includes(b)) ? String(a).replace(b, c) : a);

const initSentry = () => {
  log.debug('[initSentry] ');
  Sentry.init({
    Vue,
    attachProps: true,
    logErrors: true,
    environment: NODE_ENV,
    dsn: 'https://3e312a60dbdf450b9b8a65d956beb95b@o454915.ingest.sentry.io/5630840',
    sampleRate: 1.0,
    tracingOptions: {
      trackComponents: true,
    },
    beforeSend(event, hint) {
      /** redact webGUI url */
      const currentHost = window.location.host;
      const newEvent = mapExploreDeep(
        event,
        // eslint-disable-next-line no-unused-vars
        (val, _key) => {
          if (typeof val === 'object') return val;
          return findReplace(val, currentHost, '[REDACTED]');
        },
      );
      /** determine if we should sent event or not */
      const error = hint.originalException;
      const apiGuestIssue = 'UNRAID_SENTRY_GUEST_PERMISSIONS_ISSUE';
      const alreadySent = sessionStorage.getItem(apiGuestIssue);
      const errorGuestPermissions = error
        && error.message
        && error.message.includes('guest doesn\'t have permission to access "servers"');
      if (errorGuestPermissions) {
        log.debug('[SPECIAL  SENTRY] ');
        if (!alreadySent) { // prevent same issue from being sent in the same session
          sessionStorage.setItem(apiGuestIssue, 1);
          return newEvent; // pass the event as sentry normally would without modifying it
        }
        return null; // swallow the error this time around as we already sent it this session
      }
      return newEvent; // pass the errors we arent filtering as normal
    },
  });
};

export default initSentry;
