<script>
import {
  mapActions, mapGetters, mapState, mapMutations,
} from 'vuex';
import hexToRgba from 'hex-to-rgba';
import { mixin as clickaway } from 'vue-clickaway';

import { UnraidLogoMark } from '@unraid/shared-components';
import UnraidSvgUtilSelect from '@unraid/shared-components/assets/svg/util/select.svg';
import UnraidSvgUtilNewWindow from '@unraid/shared-components/assets/svg/util/new-window.svg';
import UnraidSvgUtilEdit from '@unraid/shared-components/assets/svg/util/edit.svg';
import UnraidSvgUiWarning from '@unraid/shared-components/assets/svg/ui/warning.svg';
import KeyAction from '../components/UserProfile/KeyAction.vue';
import MyServers from '../components/UserProfile/MyServers.vue';
import MetaInfo from '../components/UserProfile/MetaInfo.vue';

import { WanIp } from '../api';
import { SAFE_JSON_PARSE } from '../consts';
import { NODE_ENV, REGWIZ_URL } from '../env';
import { log } from '../log';

import i18n from '../i18n';
import store from '../store';
import initSentry from '../sentry';

export default {
  i18n,
  store,
  name: 'UserProfile',
  mixins: [clickaway],
  components: {
    UnraidLogoMark,
    KeyAction,
    MetaInfo,
    MyServers,
    UnraidSvgUtilSelect,
    UnraidSvgUtilNewWindow,
    UnraidSvgUtilEdit,
    UnraidSvgUiWarning,
  },
  props: {
    serverstate: String,
    serverdesc: String,
    displaydesc: {
      type: String,
      default: '',
    },
    servermodel: String,
    serverupdate: Boolean,
    banner: {
      type: String,
      default: '',
    },
    theme: {
      type: String,
      default: 'black',
    }, // black, gray and white, azure
    textcolor: String,
    bgcolor: [String, Number],
    metacolor: String,
    uptime: [Number, String], // these are run through `parseInt(x, 10)`
    expiretime: [Number, String], // these are run through `parseInt(x, 10)`
    apikey: String, // used for graphql call
    regWizTime: String, // if this is set we know the server has been registered at some point
    hideotherservers: { // used for screenshots. If 'true' then other servers will not be shown in UPC dropdown
      type: String,
      default: '',
    },
    locale: String,
    localeMessages: String,
    sendCrashInfo: String,
    csrf: String,
    hideMyServers: {
      type: String,
      default: null,
    },
    propStateEndpoint: String,
  },
  data: () => ({
    NODE_ENV,
    dropdownVisible: false,
    dropdownTimeout: '',
    errorStateMessageVisible: true,
  }),
  computed: {
    ...mapState([
      'avatar',
      'isLaunchpadOpen',
      'isPopUpOpen',
      'guid',
      'guidRegistered',
      'popUpWindow',
      'regAtOpen',
      'purchaseWindow',
      'registered',
      'servername',
      'serverip',
      'state',
      'wanip',
      'username',
      'darkTheme',
      'lightTheme',
      'extendTrialPayload',
      'recoverKeyPayload',
      'replaceKeyPayload',
      'signedOutWithKey',
      'signInPayload',
      'signUpPayload',
      'troubleshootPayload',
      'wizardWindow',
      'messages',
      'myServersEnv',
      'triggerUPC',
      'stateEndpoint',
    ]),
    ...mapGetters(['stateData']),
    darkMode() {
      return (this.theme === 'black' || this.theme === 'azure');
    },
    usernameButtonText() {
      if (this.state === 'ENOKEYFILE') return this.$t('getStarted');
      if (this.stateData.error?.signInToFix && !this.registered) return this.$t('signIn');
      if (this.stateData.error && !this.stateData.error.signInToFix && !this.state === 'EEXPIRED') return this.$t('fixError');
      if (this.registered) return this.username;
      return this.$t('signIn');
    },
    usernameButtonTitle() {
      if (this.state === 'ENOKEYFILE') return this.$t('getStarted');
      if (this.stateData.error?.signInToFix && !this.registered) return this.$t('upc.signInActions.resolve');
      if (this.stateData.error && !this.stateData.error.signInToFix) return this.$t('learnMore');
      if (this.isPopUpOpen && this.isLaunchpadOpen) return this.$t('upc.confirmClosure');
      if (this.isLaunchpadOpen) return this.$t('closeLaunchpad');
      if (this.state === 'EEXPIRED') {
        if (!this.registered) {
          return this.stateData.actions.includes('extend')
            ? this.$t('upc.signInActions.purchaseKeyOrExtendTrial')
            : this.$t('upc.signInActions.purchaseKey');
        }
        return this.$t('upc.trialHasExpiredSeeOptions');
      }
      if (this.registered && this.dropdownVisible) return this.$t('upc.closeDropdown');
      if (this.registered && !this.dropdownVisible) return this.$t('upc.openDropdown');
      return this.$t('upc.opensNewHttpsWindow.signIn');
    },
    displayKeylineBelowActions() {
      return !!(this.stateData.actions.includes('purchase')
                  || this.stateData.actions.includes('upgrade')
                  || this.stateData.actions.includes('recover')
                  || this.stateData.actions.includes('replace')
                  || this.stateData.actions.includes('extend')
                  || NODE_ENV === 'development');
    },
    extraLinks() {
      return [ // an empty URL will result in a dividing keyline instead of an empty link
        /* eslint-disable */
        ...!this.hideMyServers || this.stateData.error.message ? [
          { url: '', text: '', external: false },
        ]: [],
        ...!this.hideMyServers ? [
          {
            url: this.myServersEnv ? 'https://forums.unraid.net/my-servers-staging/' : 'https://forums.unraid.net/my-servers/',
            text: this.$t('upc.extraLinks.myServers'),
            external: true,
            title: this.$t('upc.extraLinks.newTab', [this.$t('upc.extraLinks.myServers')]),
          }] : [],
        {
          url: 'https://forums.unraid.net/',
          text: this.$t('upc.extraLinks.forums'),
          external: true,
          title: this.$t('upc.extraLinks.newTab', [this.$t('upc.extraLinks.forums')]),
        },
        {
          url: 'https://discord.gg/N2jwxjKb8W',
          text: 'Unraid Discord',
          external: true,
          title: this.$t('upc.extraLinks.newTab', ['Unraid Discord']),
        },
        {
          url: '/Settings/ManagementAccess#UnraidNetSettings',
          text: this.$t('upc.extraLinks.settings.text'),
          settings: true,
          title: this.$t('upc.extraLinks.settings.title'),
        },
        /* eslint-enable */
      ];
    },
    devEnv() { // for non prod UPC display an icon in the UPC dropdown
      let item = NODE_ENV; // default
      /** check for the UPC_ENV cookie set via the `upcEnv()` function in the webGUI > dev console */
      const ckName = 'UPC_ENV=';
      const ckArr = document.cookie.split(';');
      // eslint-disable-next-line no-plusplus
      for (let i = 0; i < ckArr.length; i++) {
        const tmp = ckArr[i].trim();
        if (tmp.indexOf(ckName) === 0) item = tmp.substring(ckName.length, tmp.length);
      }
      if (item && item !== 'production') return `UPC_ENV  ${item}`;
      return null;
    },
  },
  methods: {
    usernameAvatarButton() {
      if (this.state === 'ENOKEYFILE') {
        this.toggleLaunchpad();
        return;
      }
      if ((this.stateData.error.signInToFix || !this.stateData.error)
        && !this.registered) {
        this.openPopUp(this.signInPayload);
        return;
      }
      if (this.signedOutWithKey) {
        if (this.guidRegistered || this.regWizTime) this.openPopUp(this.signInPayload);
        else this.openPopUp(this.wizardWindow); // user will be redirected to Sign In / Up after GUID Validation in pop-up
        return;
      }
      this.dropdownVisible = !this.dropdownVisible;
    },

    ...mapActions([
      'closePopUp',
      'openPopUp',
      'receivePostMessage',
      'setStateFromWebComponentProp',
      'updateUpcFromStatePhp',
      'validateGuid',
    ]),
    ...mapMutations([
      'SET_FULL_SERVER_STATE',
      'SET_EXPIRE_TIME',
      'SET_REGWIZ_TIME',
      'SET_WANIP',
      'SET_IS_LAUNCHPAD_OPEN',
      'SET_API_KEY',
      'SET_SENTRY_ENABLED',
      'SET_DARK_MODE',
      'SET_LOCALE',
      'SET_CSRF',
      'SET_STATE_ENDPOINT',
    ]),
    closeDropdown() {
      this.dropdownVisible = false;
    },
    dropdownMouseEnter() {
      clearTimeout(this.dropdownTimeout);
    },
    dropdownMouseLeave() {
      this.dropdownTimeout = setTimeout(() => { this.dropdownVisible = false; }, 1000);
    },
    /**
     * @todo - make in action
     */
    async getWanIp() {
      if (this.wanip) return;
      this.SET_WANIP(await WanIp.text());
    },
    /**
     * @method: autoTriggerLaunchpad
     * @description: If the user hasn't seen the launchpad in this browser session let's make them see it. GOT EM ! !
     */
    autoTriggerLaunchpad() {
      setTimeout(() => {
        const mataNe = `${this.servername}_${this.guid ? this.guid.slice(-12) : 'NO_GUID'}`;
        if (!sessionStorage.getItem(mataNe) && this.state === 'ENOKEYFILE') {
          sessionStorage.setItem(mataNe, ''); // "" aka "mata ne" aka informal "bye, see you later" in Japanese ;)
          if (!this.isLaunchpadOpen) this.toggleLaunchpad();
        }
      }, 100);
    },
    /**
     * @todo explain yo'self foo
     */
    toggleLaunchpad() {
      // do we need to inject the launchpad before showing it?
      const wcName = 'unraid-launchpad';
      if (!document.getElementsByTagName(wcName).length) {
        const body = document.getElementsByTagName('body')[0];
        const lp = document.createElement(wcName);
        body.appendChild(lp);
      }
      const theWorks = () => {
        if (this.dropdownVisible) this.dropdownVisible = false; // if we open the launchpad from the UPC dropdown, close the dropdown
        if (this.isPopUpOpen) this.closePopUp();
        this.SET_IS_LAUNCHPAD_OPEN(!this.isLaunchpadOpen);
      };
      // since the user is two layers deep, confirm closure
      if (this.isPopUpOpen && this.isLaunchpadOpen) {
        // eslint-disable-next-line no-alert
        if (window.confirm(this.$t('upc.pleaseConfirmClosureYouHaveOpenPopUp'))) theWorks();
      } else {
        theWorks();
      }
    },
    /**
     * @method: initUserProfile
     * @description
     */
    async initUserProfile() {
      this.setStateFromWebComponentProp(this.serverstate);
      if (this.sendCrashInfo === 'yes') {
        this.SET_SENTRY_ENABLED(true);
        initSentry();
      }
      if (this.apikey) this.SET_API_KEY(this.apikey);
      else {
        const msg1 = 'Sign In to your Unraid.net account to perform license key actions and to receive real-time server monitoring with My Servers.';
        const msg2 = 'Learn more at https://unraid.net/product/my-servers.';
        const msgStyles = [
          'color: white',
          'background: linear-gradient(to right, #E22828 0%, #FF8C2F 100%)',
          'font-size: 12px',
          'padding: 6px',
          'font-family: monospace',
        ].join(';');
        log.log(`%c${msg1}`, msgStyles);
        log.log(`%c${msg2}`, msgStyles);
      }
      if (this.expiretime) this.SET_EXPIRE_TIME(this.expiretime);
      if (this.regWizTime) this.SET_REGWIZ_TIME(this.regWizTime);
      if (this.localeMessages) {
        log.debug('[this.locale] %o', this.locale);
        const [err, upcMsgs] = SAFE_JSON_PARSE(decodeURIComponent(this.localeMessages));
        if (err) log.error('[initUserProfile.localeMessages] %s', 'Unable to set locale and locale messages');
        this.$i18n.locale = this.locale;
        this.$i18n.setLocaleMessage(this.locale, upcMsgs[this.locale]);
        log.debug(`[initUserProfile.$i18n] ${this.$i18n.locale} locale & messages set %o`, upcMsgs[this.locale]);
        this.SET_LOCALE(this.locale);
      }
      if (this.csrf) this.SET_CSRF(this.csrf);
      if (this.propStateEndpoint) this.SET_STATE_ENDPOINT(this.propStateEndpoint); // overwrite the state.php endpoint, as the dir path may have changed
      if (!this.hideMyServers) { // dont poll when not showing my servers
        await this.getWanIp();
        if (REGWIZ_URL !== window.location.origin) {
          await this.updateUpcFromStatePhp();
          // update state every 45 secs
          setInterval(() => this.updateUpcFromStatePhp(), 45000);
        } else {
          log.debug('[SKIPPED] updateUpcFromStatePhp()'); // prevents an error when in UPC development env
        }
      }
      if (this.signedOutWithKey && this.regWizTime) await this.validateGuid();
      else this.autoTriggerLaunchpad();
      // save theme to store
      this.SET_DARK_MODE(this.darkMode); // this.darkMode is used to set this.$state.darkMode
      // here to listen for postMessages from pop-up window
      window.addEventListener('message', (event) => {
        this.receivePostMessage({ inject: false, event });
      }, false);
    },
  },
  watch: {
    signedOutWithKey(newVal, oldVal) { // check if we need to trigger this after changing
      if (newVal && !oldVal && this.regWizTime) this.validateGuid();
    },
    /**
     * auto close launchpad after sign in and closure of pop-up
     */
    isPopUpOpen(newVal, oldVal) {
      if (!this.isLaunchpadOpen) return;
      if ((!newVal && oldVal) && (!this.regAtOpen && this.registered)) { // if pop up is closing && we weren't registered but now are
        this.SET_IS_LAUNCHPAD_OPEN(false);
      }
    },
    /**
     * remote trigger of UPC dropdown
     */
    triggerUPC(newVal) {
      if (newVal) { // only when triggering, not when reseting value
        setTimeout(() => { this.dropdownVisible = true; }, 150);
      }
    },
  },
  created() {
    this.initUserProfile();
  },
  beforeMount() {
    const body = document.getElementsByTagName('body')[0];
    let { alpha, beta, gamma } = this.darkMode ? this.darkTheme : this.lightTheme;
    // overwrite with hex colors set in webGUI @ /Settings/DisplaySettings
    if (this.textcolor) alpha = this.textcolor;
    if (this.bgcolor) {
      beta = this.bgcolor;
      body.style.setProperty('--color-customgradient-start', hexToRgba(beta, 0));
      body.style.setProperty('--color-customgradient-end', hexToRgba(beta, 0.9));
    }
    if (this.metacolor) gamma = this.metacolor;
    body.style.setProperty('--color-alpha', alpha);
    body.style.setProperty('--color-beta', beta);
    body.style.setProperty('--color-gamma', gamma);
  },
};
</script>

<template>
  <div
    id="UnraidUPC"
    class="UnraidUPC"
    :class="{
      '--hasBanner-default': banner && !bgcolor,
      '--hasBanner-custom': banner && bgcolor,
    }">
    <div v-if="isPopUpOpen && !isLaunchpadOpen" class="absolute z-10 inset-0 flex flex-col items-end justify-center pr-6">
      <div class="text-center inline-flex flex-col items-center justify-center">
        <button @click="openPopUp()" class="text-14px button button-alt">{{ $t('backToPopUp') }}</button>
        <button @click="closePopUp()" class="hover:underline text-12px mt-4px">{{ $t('closePopUp') }}</button>
      </div>
    </div>
    <!-- server description -->
    <MetaInfo
      @purchase="openPopUp(purchaseWindow)"
      @upgrade="openPopUp(purchaseWindow)"
      @signin="openPopUp(signInPayload)"
      :class="{
        'blur opacity-30 pointer-events-none transition-all duration-150 ease-in-out': isPopUpOpen && !isLaunchpadOpen,
      }"
      :uptime="uptime"
      :expiretime="expiretime" />

    <div
      class="relative z-0 flex flex-row items-center justify-end h-full"
      :class="{
        'blur opacity-30 pointer-events-none transition-all duration-150 ease-in-out': isPopUpOpen && !isLaunchpadOpen,
    }">
      <!-- extra border classes here to help center the text with the button, since the button text has a border on it.
      without these classes even with items-center it's not actually centered due to the border space -->
      <h1 class="text-18px border-t-0 border-r-0 border-l-0 border-b-2 border-transparent">
        <template v-if="displaydesc">
          <span :title="servermodel">{{ serverdesc }}</span>
          <span class="text-grey-mid px-8px">&bull;</span>
        </template>
        <span :title="$t('lanIp', [serverip])">{{ servername }}</span>
      </h1>

      <div class="block w-2px h-24px ml-16px bg-grey-mid"></div>

      <div
        v-on-clickaway="closeDropdown"
        @mouseenter="dropdownMouseEnter()"
        @mouseleave="dropdownMouseLeave()"
        class="relative flex items-center justify-end h-full"
      >
        <button
          @click="usernameAvatarButton()"
          :class="{
            'text-orange-dark': isLaunchpadOpen || (!username && !stateData.error),
            'text-alpha': username && !isLaunchpadOpen,
            'text-red': stateData.error,
          }"
          class="UnraidUPC-username group text-18px hover:text-alpha focus:text-alpha border border-transparent flex flex-row justify-end items-center h-full px-16px outline-none focus:outline-none"
          :title="usernameButtonTitle"
        >
          <UnraidSvgUiWarning v-if="stateData.error || state === 'EEXPIRED'" class="text-red fill-current relative w-24px h-24px mr-8px" />
          <span class="inline-flex items-center bg-transparent border-t-0 border-l-0 border-r-0 border-b-2 border-transparent group-hover:border-orange-dark group-focus:border-orange-dark transition-colors duration-150 ease-in-out">
            {{ usernameButtonText }}
            <UnraidSvgUtilSelect v-if="registered && !isLaunchpadOpen" class="fill-current w-8px h-8px ml-8px" />
          </span>
          <!-- Signed In Avatar -->
          <span
            class="relative z-0 flex items-center justify-center w-36px h-36px ml-8px rounded-full"
            :class="{
              'bg-transparent': registered,
              'bg-gradient-to-r from-red to-orange': !registered,
            }"
          >
            <img v-if="registered" :src="avatar" :alt="`${$t('upc.avatarAlt', [username])}`" class="absolute z-10 inset-0 w-36px h-36px rounded-full overflow-hidden">
            <UnraidLogoMark v-else gradient-start="#fff" gradient-stop="#fff" class="relative z-0 w-28px" />
          </span>
        </button>
        <transition name="slideVerticalFade" mode="out-in">
          <div
            v-show="dropdownVisible"
            class="UnraidUPC-dropdown"
            :class="{
              'border-grey-darkest': darkMode,
              'border-grey-light': !darkMode,
            }"
          >
            <header v-if="!hideMyServers" class="text-14px pt-16px px-16px rounded-t">
              <h5 class="text-18px leading-none inline-flex flex-row items-center">
                <span class="font-semibold">{{ $t('upc.myServers.heading') }}</span>
                <span class="text-grey-mid text-10px uppercase ml-8px py-4px px-6px border-2 border-grey-mid rounded-full">{{ $t('upc.myServers.beta') }}</span>
                <span v-if="myServersEnv" class="ml-8px" :title="`API  ${myServersEnv}`"></span>
                <span v-if="devEnv" class="ml-8px" :title="devEnv"></span>
              </h5>
            </header>
            <ul class="list-reset my-8px p-0">
              <template v-if="NODE_ENV === 'development'">
                <li>
                  <KeyAction @click="toggleLaunchpad()" text="[DEV] Launchpad" :external="false" />
                </li>
                <li>
                  <KeyAction @click="openPopUp(wizardWindow)" text="[DEV] Pop-up" />
                </li>
              </template>
              <template v-if="displayKeylineBelowActions">
                <li>
                  <KeyAction v-if="stateData.actions.includes('purchase')"
                    @click="openPopUp(purchaseWindow)"
                    :text="$t('actions.purchase')" />
                  <KeyAction v-if="stateData.actions.includes('upgrade')"
                    @click="openPopUp(purchaseWindow)"
                    :text="$t('actions.upgrade')" />
                  <KeyAction v-if="stateData.actions.includes('recover')"
                    @click="openPopUp(recoverKeyPayload)"
                    :text="$t('actions.recover')" />
                  <KeyAction v-if="stateData.actions.includes('replace')"
                    @click="openPopUp(replaceKeyPayload)"
                    :text="$t('actions.replace')" />
                  <KeyAction v-if="stateData.actions.includes('extend')"
                    @click="openPopUp(extendTrialPayload)"
                    :text="$t('actions.extend')" />
                </li>
                <li class="my-8px mx-12px">
                  <hr class="w-full h-2px bg-gradient-to-r from-red to-orange shadow-none border-none rounded-none" />
                </li>
              </template>

              <MyServers
                v-if="!hideMyServers || stateData.error.message"
                :hideMyServers="hideMyServers"
                :errorStateMessageVisible="errorStateMessageVisible"
                :hideotherservers="hideotherservers"
                @contact-support="openPopUp(troubleshootPayload)"
                @toggle-error="errorStateMessageVisible = !errorStateMessageVisible" />

              <template v-for="(link, index) in extraLinks">
                <li v-if="link.url" :key="index">
                  <a :href="link.url" :target="`${link.external ? '_blank' : ''}`" :title="link.title" class="flex flex-row items-center">
                    <UnraidSvgUtilNewWindow v-if="link.external" class="fill-current w-12px h-12px mr-8px" />
                    <UnraidSvgUtilEdit v-if="link.settings" class="fill-current w-12px h-12px mr-8px" />
                    {{ link.text }}
                  </a>
                </li>
                <div v-else :key="index" class="mx-12px">
                  <hr class="w-full h-2px my-8px bg-gradient-to-r from-red to-orange shadow-none border-none rounded-none" />
                </div>
              </template>

            </ul>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<style lang="postcss">
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  --color-alpha: #1c1b1b;
  --color-beta: #f2f2f2;
  --color-customgradient-start: rgba(242, 242, 242, .0);
  --color-customgradient-end: rgba(242, 242, 242, .85);
}

hr {
  @apply w-full shadow-none border-none rounded-none;
}

.UnraidUPC {
  @apply text-alpha;
  @apply relative z-20 flex flex-col h-full pl-80px rounded;

  color: var(--color-alpha);

  .SelectContainer::after {
    right: 0;
  }
}

.--hasBanner-default {
  background-image: linear-gradient(90deg, rgba(0, 0, 0, 0) 0, var(--color-beta) 12%);
}

.--hasBanner-custom {
  background-image: linear-gradient(90deg, var(--color-customgradient-start) 0, var(--color-customgradient-end) 12%);
}

.UnraidUPC-dropdown {
  @apply text-beta absolute z-30 right-0 mr-8px bg-alpha border rounded-lg min-w-250px max-w-300px shadow-2xl;

  top: 95%;

  &::before {
    @apply absolute z-20 block;

    content: '';
    width: 0;
    height: 0;
    top: -10px;
    right: 25%;
    border-right: 11px solid transparent;
    border-bottom: 11px solid var(--color-alpha);
    border-left: 11px solid transparent;
  }

  button,
  a {
    @apply text-left text-16px text-beta w-full px-16px py-8px cursor-pointer;
  }

  .link-error {
    @apply text-red;
  }

  .btn-emphasize,
  button:hover,
  button:focus,
  a:hover,
  a:focus {
    @apply text-white bg-gradient-to-r from-red to-orange;

    outline: 0;
  }

  .btn-error {
    @apply text-red bg-transparent;
  }
}

.button {
  @apply text-orange;
  @apply font-semibold;
  @apply leading-none;
  @apply uppercase;
  @apply tracking-wide;
  @apply text-center;
  @apply no-underline;
  @apply inline-block;
  @apply px-10;
  @apply py-4;
  @apply bg-transparent;
  @apply rounded-sm;

  background:
    /* top */
    linear-gradient(
      to right,
      #e03237 0%,
      #fd8c3c 100%
    )
    left
    top
    no-repeat,
    /* bottom */
    linear-gradient(
      to right,
      #e03237 0%,
      #fd8c3c 100%
    )
    left
    bottom
    no-repeat,
    /* right */
    linear-gradient(
      to top,
      #e03237 0%,
      #e03237 100%
    )
