<script>
/**
 * @emits {@click} purchase
 * @emits {@click} signIn
 * @emits {@click} upgrade
 */
import { mapGetters, mapState } from 'vuex';
import dateDiff from '../../filters/dateDiff';
import dateFormat from '../../filters/dateFormat';
import i18n from '../../i18n';

export default {
  data: () => ({
    interval: null,
    parsedUptime: '',
    parsedExpireTime: '',

    dateStrings: {
      year: i18n.tc('year'),
      years: i18n.tc('years'),
      month: i18n.tc('month'),
      months: i18n.tc('months'),
      day: i18n.tc('day'),
      days: i18n.tc('days'),
      hour: i18n.tc('hour'),
      hours: i18n.tc('hours'),
      minute: i18n.tc('minute'),
      minutes: i18n.tc('minutes'),
      second: i18n.tc('second'),
      seconds: i18n.tc('seconds'),
      firstDateWasLater: i18n.tc('ago'),
      delimiter: ' ',
    },
  }),
  props: {
    expiretime: {
      type: [Number, String], // these are run through `parseInt(x, 10)`
      default: null,
    },
    uptime: {
      type: [Number, String], // these are run through `parseInt(x, 10)`
      default: null,
    },
  },
  computed: {
    ...mapState([
      'registered',
      'state',
    ]),
    ...mapGetters(['stateData']),
    formattedUptime() {
      return dateFormat(this.uptime);
    },
    formattedExpireTime() {
      return dateFormat(this.expiretime);
    },
  },
  methods: {
    pluralize(num, word) { return `${num} ${this.dateStrings[word + (num === 1 ? '' : 's')]}`; },
    buildStringFromValues({
      years, months, days, hours, minutes, seconds, firstDateWasLater, displaySecondsInStringReturn,
    }) {
      const result = [];
      if (years) result.push(this.pluralize(years, 'year'));
      if (months) result.push(this.pluralize(months, 'month'));
      if (days) result.push(this.pluralize(days, 'day'));
      if (hours) result.push(this.pluralize(hours, 'hour'));
      if (minutes) result.push(this.pluralize(minutes, 'minute'));
      if (seconds && ((!years && !months && !days && !hours && !minutes) || displaySecondsInStringReturn)) result.push(this.pluralize(seconds, 'second'));
      if (firstDateWasLater) result.push(this.dateStrings.firstDateWasLater);
      return result.join(this.dateStrings.delimiter);
    },
    runTimeDiff() {
      if (this.uptime) this.parsedUptime = this.buildStringFromValues(dateDiff(this.uptime, true));
      if (this.expiretime) this.parsedExpireTime = this.buildStringFromValues(dateDiff(this.expiretime, false));
    },
    letsGo() {
      this.runTimeDiff();
      this.interval = setInterval(() => {
        this.runTimeDiff();
      }, 1000);
    },
  },
  created() {
    this.letsGo();
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
};
</script>

<template>
  <div class="text-gamma text-12px text-right font-semibold leading-normal flex flex-row items-baseline justify-end pr-16px pt-4px">
    <p v-if="state === 'EEXPIRED'" :title="$t('upc.meta.trial.expired.date', { date: formattedExpireTime })">
      {{ $t('upc.meta.trial.expired.timeDiff', { timeDiff: parsedExpireTime }) }}
    </p>
    <p v-else-if="this.expiretime > 0" :title="$t('upc.meta.trial.active.date', { date: formattedExpireTime })">
      {{ $t('upc.meta.trial.active.timeDiff', { timeDiff: parsedExpireTime }) }}
    </p>
    <p v-else class="select-all" :title="$t('upc.meta.uptime.date', { date: formattedUptime })">
      {{ $t('upc.meta.uptime.readable', { timeDiff: parsedUptime }) }}
    </p>

    <span class="px-12px">&bull;</span>

    <button
      v-if="registered && stateData.actions.includes('upgrade')"
      @click="$emit('upgrade')"
      class="text-12px text-gamma hover:text-alpha focus:text-alpha font-semibold transition-colors duration-150 ease-in-out border-t-0 border-l-0 border-r-0 border-b-2 border-transparent hover:border-orange-dark focus:border-orange-dark focus:outline-none"
      :title="$t('upc.opensNewHttpsWindow.upgrade')"
    >
      <h5>Unraid OS <em><strong>{{ stateData.humanReadable }}</strong></em></h5>
    </button>
    <h5 v-else>Unraid OS <em :class="{ 'text-red': stateData.error || state === 'EEXPIRED' }"><strong>{{ stateData.humanReadable }}</strong></em></h5>

    <button
      v-if="registered && stateData.actions.includes('purchase')"
      @click="$emit('purchase')"
      class="text-12px text-orange-dark font-semibold hover:text-alpha focus:text-alpha ml-12px transition-colors duration-150 ease-in-out border-t-0 border-l-0 border-r-0 border-b-2 border-transparent hover:border-orange-dark focus:border-orange-dark focus:outline-none"
      :title="$t('upc.opensNewHttpsWindow.purchase')"
    >{{ $t('actions.purchase') }}</button>
  </div>
</template>
