<script>
import { mapActions, mapGetters, mapState } from 'vuex';

import i18n from '../i18n';
import store from '../store';
// eslint-disable-next-line import/no-unresolved
// import '../sentry';

export default {
  i18n,
  store,
  name: 'UpcTrigger',
  props: [],
  data: () => ({}),
  computed: {
    ...mapGetters(['stateData', 'stateDataKeyActions']),
    ...mapState([
      'isPopUpOpen',
      'registered',
      'state',
      'triggerUPC',
      'wizardWindow',
    ]),
    copy() {
      if (!this.registered) return 'You must be signed in to Unraid.net to perform key actions.';
      if (this.state === 'BASIC' || this.state === 'PLUS') return 'To support more storage devices as your server grows, click Upgrade Key in the User Profile located at the top right of the webGUI on every page.';
      return 'Key actions are done in the User Profile located at the top right of the webGUI on every page.';
    },
    buttonText() {
      if (this.stateData.error) return this.$t('fixError');
      if (this.isPopUpOpen) return this.$t('backToPopUp');
      if (this.state === 'ENOKEYFILE') return this.$t('getStarted');
      if (!this.registered) return this.$t('signIn');
      // return this.$t('upc.trigger');
      if (this.stateDataKeyActions) return 'Open User Profile';
      return '';
    },
    buttonTitle() {
      if (this.stateDataKeyActions) return 'Open User Profile';
      return this.$t('upc.opensNewHttpsWindow.base', [this.buttonText]);
    },
  },
  methods: {
    ...mapActions(['jumpToUpc', 'closePopUp', 'openPopUp']),
    btnClick() {
      if (!this.registered || this.isPopUpOpen) return this.openPopUp(this.wizardWindow);
      return this.jumpToUpc();
    },
  },
};
</script>

<template>
  <transition appear name="fade" mode="out-in">
    <div v-if="registered !== null && buttonText" class="flex flex-wrap pl-12px">
      <p class="w-7/20 pr-12px">{{ copy }}</p>
      <div class="w-13/20 inline-flex flex-row">
        <button
          @click="btnClick()"
          type="button"
          :title="buttonTitle"
          :disabled="triggerUPC"
        >{{ buttonText }}</button>
        <transition name="fade" mode="out-in">
          <button
            v-if="isPopUpOpen"
            @click="closePopUp()"
            class="ml-8px"
            type="button"
          >{{ $t('closePopUp') }}</button>
        </transition>
      </div>
    </div>
  </transition>
</template>

