<template>
  <transition appear name="fade" mode="in-out">
    <div v-show="isLaunchpadOpen" class="fixed inset-0 z-10 flex flex-col justify-start items-end mt-90px">
      <transition appear name="fade" mode="in-out">
        <div
          v-show="isLaunchpadOpen"
          @click="SET_IS_LAUNCHPAD_OPEN(false)"
          class="absolute inset-0 z-0 bg-beta opacity-70 cursor-pointer"
          :title="$t('closeLaunchpad')"></div>
      </transition>
      <transition appear name="slideVerticalFade" mode="in-out">
        <main v-show="isLaunchpadOpen" class="TopBlip text-beta bg-alpha border-grey-darkest text-center relative z-10 w-full max-w-4xl mr-8px p-24px md:p-32px lg:px-40px shadow-md rounded-lg">
          <LaunchpadActionRequired />
        </main>
      </transition>
    </div>
  </transition>
</template>

<script>
import {
  mapState, mapActions, mapMutations, mapGetters,
} from 'vuex';

import i18n from '../i18n';
import store from '../store';
// eslint-disable-next-line import/no-unresolved
// import '../sentry';

import LaunchpadActionRequired from '../layouts/Launchpad/ActionRequired.vue';

export default {
  i18n,
  store,
  name: 'Launchpad',
  props: ['serverstate', 'webguiTranslations'],
  components: {
    LaunchpadActionRequired,
  },
  computed: {
    ...mapState([
      'guid',
      'isLaunchpadOpen',
    ]),
    ...mapGetters(['server']),
  },
  methods: {
    ...mapActions([
      'setStateFromWebComponentProp',
      'validateGuid',
    ]),
    ...mapMutations(['SET_IS_LAUNCHPAD_OPEN']),
  },
  watch: {
    signedOutWithKey(newVal, oldVal) { // check if we need to trigger this after changing
      if (newVal && !oldVal) this.validateGuid();
    },
  },
  created() {
    // only set state from prop when present
    if (this.serverstate) this.setStateFromWebComponentProp(this.serverstate);
    // check for guid then validate, otherwise keep checking for guid
    if (this.guid) this.validateGuid();
    else {
      const waitForGuid = setInterval(() => {
        if (!this.guid) return;
        clearInterval(waitForGuid);
        this.validateGuid();
      }, 250);
    }
  },
};
</script>
