<script>
import {
  mapActions, mapGetters, mapState,
} from 'vuex';

import i18n from '../i18n';
import store from '../store';
import { log } from '../log';
// eslint-disable-next-line import/no-unresolved
// import '../sentry';

export default {
  i18n,
  store,
  name: 'Authed',
  props: ['propRegistered'],
  data: () => ({
    regChanged: false,
    popUpClosedTimer: null,
  }),
  computed: {
    ...mapState([
      'state',
      'registered',
      'isPopUpOpen',
      'signOutPayload',
      'wizardWindow',
    ]),
    ...mapGetters(['stateData']),
    computedReg() { // rely on prop before the VUEX state kicks in
      if (this.registered === null) {
        return !!this.propRegistered;
      }
      return this.registered;
    },
    buttonPayload() {
      if (this.stateData.error) return this.wizardWindow;
      if (this.computedReg) return this.signOutPayload;
      return this.wizardWindow;
    },
    buttonText() {
      if (this.stateData.error) return this.$t('fixError');
      if (this.isPopUpOpen) return this.$t('backToPopUp');
      if (this.state === 'ENOKEYFILE') return this.$t('getStarted');
      if (this.computedReg) return this.$t('signOut');
      return this.$t('signIn');
    },
    buttonTitle() {
      return this.$t('upc.opensNewHttpsWindow.base', [this.buttonText]);
    },
  },
  watch: {
    /**
     * @description determines whether we should reload the webGUI when closing pop-up
     */
    isPopUpOpen(newVal) {
      if (!newVal && this.regChanged) { // only watching the closure of the pop-up
        log.debug('[REGISTERED CHANGED]  reload webGUI settings');
        window.location.reload();
      }
    },
    registered(newVal, oldVal) {
      if (oldVal !== null && newVal !== this.propRegistered) {
        this.regChanged = true;
        log.debug('[REGISTERED CHANGED]');
        /** check if we should reload because the webGUI was slightly too slow
         * or because the settings page is open in another tab/window */
        if (!this.isPopUpOpen) {
          log.debug('[REGISTERED CHANGED]  reload webGUI settings');
          window.location.reload();
        }
      }
    },
  },
  methods: {
    ...mapActions([
      'closePopUp',
      'openPopUp',
    ]),
  },
};
</script>

<template>
  <transition appear name="fade" mode="out-in">
    <div>
      <button
        @click="openPopUp(buttonPayload)"
        type="button"
        :title="buttonTitle"
      >{{ buttonText }}</button>
      <transition name="fade" mode="out-in">
        <button
          v-if="isPopUpOpen"
          @click="closePopUp()"
          class="ml-8px"
          type="button"
        >{{ $t('closePopUp') }}</button>
      </transition>
    </div>
  </transition>
</template>

