/* eslint-disable no-unused-vars */
/* eslint-disable import/prefer-default-export */
import gql from 'graphql-tag';

import { getGraphqlClient } from './client';
import { log } from '../../log';

export const fetchServers = async (context) => {
  log.debug('[fetchServers]');
  context.commit('SET_MY_SERVERS_LOADING', true);
  /**
   * Initial query for My Servers data
   * @see: https://www.apollographql.com/docs/react/api/core/ApolloClient/#ApolloClient.query
   */
  const response = await getGraphqlClient(context).query({
    query: gql`query initialGetServers {
      servers {
        name
        guid
        status
        remoteurl
        localurl
        lanip
        wanip
        owner {
          avatar
          url
          username
        }
      }
    }`,
  });
  log.debug('[fetchServers] response %o', response);
  const { data } = response;
  if (data) {
    context.commit('SET_GRAPH_ONLINE', true);
    context.commit('SET_MY_SERVERS', data.servers);
    context.commit('SET_MY_SERVERS_LOADING', false);
  }
  /**
   * Start subscription
   * @see: https://www.apollographql.com/docs/react/api/core/ApolloClient/#ApolloClient.subscribe */
  getGraphqlClient(context).subscribe({
    query: gql`subscription getServers {
      servers {
        name
        guid
        status
        remoteurl
        localurl
        lanip
        wanip
        owner {
          avatar
          url
          username
        }
      }
    }`,
  }).subscribe({ /** `subscribe().subscribe()` I know, wtf right? */
    next(res) {
      log.debug('[new subscription data] %o', res);
      // if graph went offline, let's go back online
      if (!context.state.graphOnline) context.commit('SET_GRAPH_ONLINE', true);
      context.commit('SET_MY_SERVERS', res.data.servers);
    },
    error(err) {
      context.commit('SET_MY_SERVERS_ERROR', err);
      return context.state.myServersError;
    },
  });
};
