import {
  CAN_POST_MESSAGE, REMOVE_EMPTY, THE_SMARTASS,
} from '../consts';

import i18n from '../i18n';

/**
 * @property actions: {Array} - ['extend', 'purchase', 'recover', 'replace', 'upgrade']
 * @property humanReadable {String}
 * @property heading {String}
 * @property message {String}
 * @property error {Boolean|Object} // see `ENOFLASH` below for error object example
 * @property error.heading {String}
 * @property error.message {String}
 * @property error.signInToFix {Boolean}
 */
const stateDataDefault = {
  actions: [],
  humanReadable: '',
  heading: '',
  message: '',
  messageCenter: false,
  error: false,
  withKey: false,
};

const ENOFLASH = {
  ...stateDataDefault,
  humanReadable: i18n.tc('stateData.ENOFLASH.humanReadable'),
  error: {
    heading: i18n.tc('stateData.ENOFLASH.error.heading'),
    message: i18n.tc('stateData.ENOFLASH.error.message'),
    signInToFix: false,
  },
};

// eslint-disable-next-line import/prefer-default-export
export const getters = {
  requireGuidValidation: (state) => {
    if (CAN_POST_MESSAGE) return state.guidRegistered;
    return true; // this means we're in the UPC and can pass guid validation
  },
  trialExtensionEligible: state => (!!(!state.reggen || state.reggen < 2)), // used below to allow button actions
  signedOutWithKey: (state, self) => (!!(self.stateData.withKey && !state.registered)), // watched in Launchpad & UPC
  stateData: (state, self) => {
    switch (state.state) {
    case 'ENOKEYFILE': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
      ],
      humanReadable: i18n.tc('stateData.ENOKEYFILE.humanReadable'),
      heading: state.registered
        ? i18n.tc('stateData.ENOKEYFILE.heading.registered')
        : i18n.tc('stateData.ENOKEYFILE.heading.notRegistered'),
      message: state.registered
        ? i18n.tc('stateData.ENOKEYFILE.message.registered')
        : i18n.tc('stateData.ENOKEYFILE.message.notRegistered'),
      messageCenter: true,
    };
    case 'TRIAL': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        state.registered ? 'purchase' : null,
        state.registered ? 'continueTrial' : null,
      ],
      humanReadable: i18n.tc('stateData.TRIAL.humanReadable'),
      heading: i18n.tc('stateData.TRIAL.heading'),
      message: i18n.tc('stateData.TRIAL.message'),
      withKey: true,
    };
    case 'EEXPIRED': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        // both below used to use `&& state.guidRegistered`
        (state.registered && self.trialExtensionEligible) ? 'extend' : null,
        state.registered ? 'purchase' : null,
      ],
      humanReadable: i18n.tc('stateData.EEXPIRED.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EEXPIRED.heading'),
        message: self.trialExtensionEligible
          ? i18n.tc('stateData.EEXPIRED.message.extensionEligible')
          : i18n.tc('stateData.EEXPIRED.message.extensionNotEligible'),
      },
    };
    case 'BASIC': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        (state.registered && self.requireGuidValidation) ? 'upgrade' : null,
      ],
      humanReadable: i18n.tc('stateData.BASIC.humanReadable'),
      heading: i18n.tc('basicPlusPro.heading'),
      // eslint-disable-next-line no-nested-ternary
      message: !state.registered
        ? i18n.tc('basicPlusPro.message.registered')
        : (state.guidRegistered)
          ? i18n.tc('basicPlusPro.message.upgradeEligible')
          : '',
      messageCenter: !state.registered,
      withKey: true,
    };
    case 'PLUS': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        (state.registered && self.requireGuidValidation) ? 'upgrade' : null,
      ],
      humanReadable: i18n.tc('stateData.PLUS.humanReadable'),
      heading: i18n.tc('basicPlusPro.heading'),
      // eslint-disable-next-line no-nested-ternary
      message: !state.registered
        ? i18n.tc('basicPlusPro.message.registered')
        : (state.guidRegistered)
          ? i18n.tc('basicPlusPro.message.upgradeEligible')
          : '',
      messageCenter: !state.registered,
      withKey: true,
    };
    case 'PRO': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        state.registered ? 'close' : null,
      ],
      humanReadable: i18n.tc('stateData.PRO.humanReadable'),
      heading: i18n.tc('basicPlusPro.heading'),
      message: !state.registered
        ? i18n.tc('basicPlusPro.message.registered')
        : '',
      messageCenter: !state.registered,
      withKey: true,
    };
    case 'EGUID': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        state.registered ? 'purchase' : null,
        state.registered ? 'replace' : null, // we always show replace key button until we rightfully know we shouldn't
      ],
      humanReadable: i18n.tc('stateData.EGUID.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EGUID.error.heading'),
        // eslint-disable-next-line no-nested-ternary
        message: ((state.replaceable === null)
          ? i18n.tc('stateData.EGUID.error.message.replacementEligible')
          : (state.replaceable)
            ? i18n.tc('stateData.EGUID.error.message.replacementEligible')
            : `${i18n.tc('stateData.EGUID.error.message.default')} <br class="mb-2">${i18n.tc('stateData.EGUID.error.message.replacementIneligible')}`),
        signInToFix: true,
      },
    };
    case 'EGUID1': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        state.registered ? 'purchase' : null,
      ],
      humanReadable: i18n.tc('stateData.EGUID1.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EGUID1.error.heading'),
        message: i18n.tc('stateData.EGUID1.error.message'),
        signInToFix: true,
      },
    };
    case 'ENOKEYFILE2': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        state.registered ? 'purchase' : null,
        (state.registered && self.requireGuidValidation) ? 'recover' : null,
      ],
      humanReadable: i18n.tc('stateData.ENOKEYFILE2.humanReadable'),
      error: {
        heading: i18n.tc('stateData.ENOKEYFILE2.error.heading'),
        message: i18n.tc('stateData.ENOKEYFILE2.error.message'),
        signInToFix: true,
      },
    };
    case 'ETRIAL': return {
      ...stateDataDefault,
      actions: [
        state.registered ? 'signOut' : 'register',
        (state.registered && self.requireGuidValidation) ? 'purchase' : null,
      ],
      humanReadable: i18n.tc('stateData.ETRIAL.humanReadable'),
      error: {
        heading: i18n.tc('stateData.ETRIAL.error.heading'),
        message: i18n.tc('stateData.ETRIAL.error.message'),
        signInToFix: true,
      },
    };
      /**
       * Begin states without sign in / key actions
       */
    case 'ENOKEYFILE1': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.ENOKEYFILE1.humanReadable'),
      error: {
        heading: i18n.tc('stateData.ENOKEYFILE1.error.heading'),
        message: i18n.tc('stateData.ENOKEYFILE1.error.message'),
        signInToFix: false,
      },
    };
    case 'ENOFLASH': return ENOFLASH;
    case 'ENOFLASH1': return ENOFLASH;
    case 'ENOFLASH2': return ENOFLASH;
    case 'ENOFLASH3': return ENOFLASH;
    case 'ENOFLASH4': return ENOFLASH;
    case 'ENOFLASH5': return ENOFLASH;
    case 'ENOFLASH6': return ENOFLASH;
    case 'ENOFLASH7': return ENOFLASH;
    case 'EBLACKLISTED': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.EBLACKLISTED.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EBLACKLISTED.error.heading'),
        message: i18n.tc('stateData.EBLACKLISTED.error.message'),
        signInToFix: false,
      },
    };
    case 'EBLACKLISTED1': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.EBLACKLISTED1.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EBLACKLISTED1.error.heading'),
        message: i18n.tc('stateData.EBLACKLISTED1.error.message'),
        signInToFix: false,
      },
    };
    case 'EBLACKLISTED2': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.EBLACKLISTED2.humanReadable'),
      error: {
        heading: i18n.tc('stateData.EBLACKLISTED2.error.heading'),
        message: i18n.tc('stateData.EBLACKLISTED2.error.message'),
        signInToFix: false,
      },
    };
    case 'ENOCONN': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.ENOCONN.humanReadable'),
      error: {
        heading: i18n.tc('stateData.ENOCONN.error.heading'),
        message: i18n.tc('stateData.ENOCONN.error.message'),
        signInToFix: false,
      },
    };
    case 'STALE': return {
      ...stateDataDefault,
      humanReadable: i18n.tc('stateData.STALE.humanReadable'),
      error: {
        heading: i18n.tc('stateData.STALE.error.heading'),
        message: i18n.tc('stateData.STALE.error.message'),
        signInToFix: false,
      },
    };
    default: return { ...stateDataDefault };
    }
  },
  stateDataKeyActions: (_state, self) => !!(self.stateData.actions.includes('purchase')
    || self.stateData.actions.includes('upgrade')
    || self.stateData.actions.includes('recover')
    || self.stateData.actions.includes('replace')
    || self.stateData.actions.includes('extend')),
  keyTypeForPurchase: (state) => { /** @NOTE - DO NOT TRANSLATE THESE !!! These string values are used in the Craft checkout flow */
    switch (state.state) {
    case 'BASIC': return 'Basic';
    case 'PLUS': return 'Plus';
    case 'PRO': return 'Pro';
    default: return 'Trial';
    }
  },
  // using REMOVE_EMPTY so that the data that's sent to the pop-up window is santized of null or undefined values
  server: (state, self) => (REMOVE_EMPTY({
    guid: state.guid ?? THE_SMARTASS, // so we can see Newman errors
    regGuid: state.regGuid, // so we can see Newman errors
    guidRegistered: state.guidRegistered || state.regWizTime,
    regWizTime: state.regWizTime,
    keyfile: state.keyfile,
    keyTypeForPurchase: self.keyTypeForPurchase,
    flashproduct: state.flashproduct,
    flashvendor: state.flashvendor,
    servername: state.servername,
    serverip: state.serverip,
    internalip: state.serverip,
    site: state.site,
    state: state.state,
    error: state.error,
    internalport: state.internalport,
    protocol: state.protocol,
    errorState: self.stateData.error,
    registered: state.registered,
    reggen: state.reggen,
    userEmail: state.userEmail,
    expiretime: state.expiretime,
    ts: state.ts,
    locale: state.locale,
    deviceCount: state.deviceCount,
    sentryEnabled: state.sentryEnabled,
    stateEndpoint: state.stateEndpoint,
  })),
};
