// import deepMerge from 'deepmerge';

import { SAFE_JSON_PARSE } from '../consts';
import { log } from '../log';

// eslint-disable-next-line import/prefer-default-export
export const mutations = {
  SET_APP_ERROR_MESSAGE(state, data) {
    log.debug('[SET_APP_ERROR_MESSAGE]: %s', data);
    state.error = !!(data);
    state.errorMessage = data;
    if (state.errorMessage) log.error('[APP_ERROR]: %s', state.errorMessage);
  },
  SET_LOADING_STATE(state, data) {
    log.debug('[SET_LOADING_STATE]: %o', data);
    state.loading = data;
  },
  SET_IS_LAUNCHPAD_OPEN(state, data) {
    log.debug('[SET_IS_LAUNCHPAD_OPEN]: %o', data);
    state.isLaunchpadOpen = data;
  },
  // server
  OVERRIDE_SERVER_STATE(state, data) {
    log.debug('[OVERRIDE_SERVER_STATE]: %o', data);
    state.state = data;
  },
  CHANGE_GUID_BLACKLISTED(state, data) {
    log.debug('[CHANGE_GUID_BLACKLISTED]: %o', data);
    state.guidBlacklisted = data;
  },
  /**
   * @note follow specific order when committing mutations for
   * - `CHANGE_GUID_REGISTERED`
   * - `CHANGE_GUID_VALID` is watched in Index.vue to see if we should redirect
   * the user to Sign In or Up after validating their GUID in the pop-up. And this
   * conditional relies on the value set by `CHANGE_GUID_REGISTERED` aka `state.guidRegistered`
   */
  CHANGE_GUID_REGISTERED(state, data) { // put before CHANGE_GUID_REGISTERED when committing
    log.debug('[CHANGE_GUID_REGISTERED]: %o', data);
    state.guidRegistered = data;
  },
  CHANGE_GUID_VALID(state, data) { // put after CHANGE_GUID_REGISTERED when committing
    log.debug('[CHANGE_GUID_VALID]: %o', data);
    state.guidValid = data;
  },
  CHANGE_REPLACEABLE(state, data) {
    log.debug('[CHANGE_REPLACEABLE]: %o', data);
    state.replaceable = data;
  },
  // Universal Pop-up Window
  CHANGE_POP_UP_WINDOW(state, data) {
    log.debug('[CHANGE_POP_UP_WINDOW]: %o', data);
    state.popUpWindow = data;
    state.isPopUpOpen = !!data;
    state.regAtOpen = null;
  },
  STATE_AT_OPEN(state, data) {
    state.regAtOpen = data;
  },
  // Reg Pop-up Window
  /**
   * @name MUTATE_KEYSERVER_RESPONSE
   * @description stores response data from key server submissions
   * @param {Object} state - Vuex state
   * @param {Object} res - response from Key Server
   * @param {string} res.apikey - 64 random chars
   * @param {boolean} res.validated
   * @param {string} res.license - url to license file
   * @param {string} res.email
   * @param {string} res.action - "recovery|new"
   */
  MUTATE_KEYSERVER_RESPONSE(state, res) {
    log.debug('[MUTATE_KEYSERVER_RESPONSE]: %o', res);
    if (res.license) state.license = res.license;
    if (res.trial) state.keyfile = res.trial;
    if (res.apikey) state.apikey = res.apikey;
    if (res.validated) state.ipsValidated = res.validated;
    if (res.email) state.userEmail = res.email;
    if (res.password) state.userPassword = res.password;
    if (res.action || res.keyServerAction) state.keyServerAction = res.action || res.keyServerAction;
    if (res.username) state.username = res.username;
    if (res.avatar) state.avatar = res.avatar;
  },
  /**
   * @name SET_TRIAL
   * @description
   * @param {object} state - vuex state https://vuex.vuejs.org/guide/state.html
   * @param {string} trial
   */
  SET_TRIAL(state, res) {
    log.debug('[SET_TRIAL]: %o', res);
    state.license = res.license;
    state.keyfile = res.trial;
    state.keyServerAction = 'extendTrial';
  },
  /**
   * @name SET_USER_EMAIL
   * @description
   * @param {object} state - vuex state https://vuex.vuejs.org/guide/state.html
   * @param {string} email
   */
  SET_USER_EMAIL(state, email) {
    log.debug('[SET_USER_EMAIL]: %s', email);
    state.userEmail = email;
  },

  SET_FULL_SERVER_STATE(state, data) {
    log.debug('[SET_FULL_SERVER_STATE] %o', data);
    /* eslint-disable */
    if (data.flashproduct) data.flashproduct = decodeURIComponent(data.flashproduct);
    if (data.flashvendor) data.flashvendor = decodeURIComponent(data.flashvendor);
    if (data.site) data.site = decodeURIComponent(data.site);
    // convert to boolean @see: https://stackoverflow.com/a/45175346/3215809
    if (data.registered || data.registered === 0) data.registered = Boolean(Number(data.registered));
    state = Object.assign(state, data);
    /* eslint-enable */
    if (data.email) state.userEmail = data.email;
  },

  SIGN_OUT(state) {
    log.debug('[SIGN_OUT]: %s', '');
    state.registered = false;
    state.license = '';
    state.keyfile = '';
    state.apikey = '';
    state.ipsValidated = false;
    state.userEmail = '';
    state.userPassword = '';
    state.username = '';
    state.avatar = '';
  },

  SUBMISSION_FROM(state, val) {
    log.debug('[SUBMISSION_FROM]: %s', val);
    state.accountAction = null; // resetting this value for /success
    state.submissionFrom = val;
  },

  SET_WANIP(state, val) {
    log.debug('[SET_WANIP]: %s', val);
    state.wanip = val;
  },

  SET_SITE(state, val) {
    log.debug('[SET_SITE]: %s', val);
    state.site = val;
  },

  SET_CSRF(state, val) {
    log.debug('[SET_CSRF]: %s', val);
    state.csrf = val;
  },

  LICENSE_PINGBACK(state, val) {
    log.debug('[LICENSE_PINGBACK]: %s', val);
    state.licenseAction = val;
  },

  ACCOUNT_PINGBACK(state, val) { // this is clear when leaving 'Success.vue'
    log.debug('[ACCOUNT_PINGBACK]: %s', val);
    state.accountAction = val;
  },
  /**
   * @param {DateTimeStamp} val  feed it `Date.now()` as this will trigger the watcher of `receivedSuccessNewServerState` in Success.vue to trigger `this.loading = false`
   */
  RECEIVED_SUCCESS_NEW_SERVERSTATE(state, val) {
    log.debug('[RECEIVED_SUCCESS_NEW_SERVERSTATE]: %o', val);
    state.receivedSuccessNewServerState = val;
  },

  SET_EXPIRE_TIME(state, val) {
    log.debug('[SET_EXPIRE_TIME]: %s', val);
    state.expiretime = val;
  },

  SET_REGWIZ_TIME(state, val) {
    log.debug('[SET_REGWIZ_TIME]: %s', val);
    state.regWizTime = val;
  },

  CHANGE_GUID_FORUMACCOUNT(state, val) {
    log.debug('[CHANGE_GUID_FORUMACCOUNT]: %s', val);
    state.forumaccount = val;
  },

  SET_LOCALE(state, val) {
    log.debug('[SET_LOCALE]: %s', val);
    state.locale = val;
  },

  LOCALE_MESSAGES(state, val) {
    const [err, data] = SAFE_JSON_PARSE(decodeURIComponent(val));
    if (err) log.warn('[LOCALE_MESSAGES]: %o', err);
    else {
      log.debug('[LOCALE_MESSAGES]: %o', data);
      state.messages = data;
    }
  },

  SET_API_KEY(state, val) {
    log.debug('[SET_API_KEY]: %s', val);
    state.apikey = val;
  },

  SET_GRAPH_ONLINE(state, val) {
    log.debug('[SET_GRAPH_ONLINE]: %o', val);
    state.graphOnline = val;
  },

  SET_MY_SERVERS(state, val) {
    log.debug('[SET_MY_SERVERS]: %o', val);
    state.myServers = [...val];
  },
  SET_MY_SERVERS_LOADING(state, val) {
    log.debug('[SET_MY_SERVERS_LOADING]: %o', val);
    state.myServersLoading = val;
  },
  SET_MY_SERVERS_ERROR(state, val) {
    if (!val) log.debug('[myServersError]', val);
    else log.error('[myServersError]: %o', val);
    state.myServersError = val;
  },
  SET_MY_SERVERS_ENV(state, val) {
    log.debug('[SET_MY_SERVERS_ENV]: %o', val);
    state.myServersEnv = val;
  },

  TRIGGER_UPC(state, val) {
    log.debug('[TRIGGER_UPC]: %o', val);
    state.triggerUPC = val;
  },

  SET_SENTRY_ENABLED(state, val) {
    log.debug('[SET_SENTRY_ENABLED]: %o', val);
    state.sentryEnabled = val;
  },

  SET_DARK_MODE(state, val) {
    log.debug('[SET_DARK_MODE]: %o', val);
    state.darkMode = val;
  },

  SET_STATE_ENDPOINT(state, val) {
    log.debug('[SET_STATE_ENDPOINT]: %o', val);
    state.stateEndpoint = val;
  },
};
